body{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(42, 44, 53);
}

span{  
  font-family: Arial, Helvetica, sans-serif;
}

button{
  box-shadow: 1px 1px 10px rgba(0, 0, 0. 0.2);
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
}

button:hover{
  opacity: 80%;
}
button:active{
  filter: brightness(80%);
}
button:disabled{
  filter: brightness(70%);
}

div{
  
  font-family: Arial, Helvetica, sans-serif;
}

.QuickToggles{
  
}

.row{
  height: 20px;
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;

}

.task_row{
  height: 40px;
  width: 95%;
  padding: 10px;
  margin-top: 2.5px;;
  margin-bottom: 2.5px;;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.8);
}

.dialogue_back{
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.4);
}

.dialogue_card{
  min-height: 100px;
  min-width: 100px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #EEEEEE;
}

.fill_center{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vh;
  flex-direction: column;
}

.taskBase{
  min-height: 20px;
  width: 70%;
  background-color: rgba(255, 255, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.QuickToggleItem{
  padding: 10px;
  margin: 5px;
  height: 35px;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.7);
}

button{
  color: white;
  border: 0;
  padding: 10px;
  font-weight: bold;
  margin: 5px;
}

.editButton{
  height: 25px;
  width: 25px;
  background-color: #2196F3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.off{
  background-color: #E91E63;
}
.on{
  background-color: #4CAF50;
}

.o{
  background-color: #2196F3;
}